@mixin block-btn($btn-class){
    .wp-block-buttons,
    .wp-block-button{
        margin-bottom: $measure*4;
        .wp-block-button__link{
            @extend .#{$btn-class} !optional;
        }
    } 
}

@mixin block-btn-modifier($btn-class-modifier) {
    .wp-block-button{
        .wp-block-button__link{
            @extend .#{$btn-class-modifier} !optional;
        }
    } 
}